import React from "react";
import "./Table.css";

const renderTransactions = trans => {
  return (
    <tbody>
      <tr className="d-none">
        <th scope="row" colSpan="5" />
      </tr>
      {trans !== null &&
        trans.map((trans, index) => {
          return (
            <tr key={index}>
              <th scope="row">{trans.primaryAccountId}</th>
              <td>{trans.tradeType}</td>
              <td>{trans.notionalAmount}</td>
              <td>{getFormattedDate(trans.tradeDate)}</td>
              <td>
                <i className={getClassName(trans.status)} />{" "}
                <span className="lq-status">{trans.status}</span>
              </td>
            </tr>
          );
        })}
    </tbody>
  );
};

const _MONTHS = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec"
];

const getFormattedDate = date => {
  if (date) {
    date = new Date(date);
    return (
      date.getDay() + " " + _MONTHS[date.getMonth()] + " " + date.getFullYear()
    );
  } else return date;
};

const getClassName = status => {
  if (status) {
    status = status.replace(/ /g, "-");
    return "lq lq-" + status.toLowerCase();
  }
};

const Table = props => {
  return (
    <table className="table table-bordered table-striped liquify-table">
      <thead className="thead-dark-ui">
        <tr>
          <th scope="col">Account</th>
          <th scope="col">Product</th>
          <th scope="col">Notional</th>
          <th scope="col">Date</th>
          <th scope="col">Status</th>
        </tr>
      </thead>
      {renderTransactions(props.transactions)}
    </table>
  );
};

export default Table;
