import React, { Component } from "react";
import _ from "lodash";

class LQSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputLabel: ""
    };
  }

  static getDerivedStateFromProps(props, state) {
    return state;
  }

  getOptions(options) {
    var arr = [];
    _.forEach(options, function(value, key) {
      arr.push(
        <option key={key} value={value.value}>
          {value.label}
        </option>
      );
    });
    return arr;
  }

  render() {
    return (
      <div className="lq-select-wrapper">
        <label className="lq-select-label">{this.props.label}</label>
        <select
          className="lq-custom-select"
          onChange={e => {
            this.props.handleChange(e);
          }}
          disabled={this.props.isDisabled}
          defaultValue={this.props.defaultValue}
        >
          {this.getOptions(this.props.options)}
        </select>
      </div>
    );
  }
}
export default LQSelect;
