import React, { Component } from "react";
import "./Input.css";

class Input extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputLabel: "input-form-control-lable input-form-control-lable-hidden",
      value: props.value
    };
  }

  static getDerivedStateFromProps(props, state) {
    if (props.value && props.value !== "") {
      state.inputLabel =
        "input-form-control-lable input-form-control-lable-active";
      state.value = props.value;
      return state;
    } else {
      state.value = "";
      return state;
    }
  }

  setFocusStyle(e) {
    if (e.target.value === "") {
      this.setState({
        inputLabel: "input-form-control-lable input-form-control-lable-active"
      });
    }
  }
  setBlurStyle(e) {
    if (e.target.value !== "") {
      this.setState({
        inputLabel: "input-form-control-lable input-form-control-lable-active"
      });
    } else {
      this.setState({
        inputLabel: "input-form-control-lable input-form-control-lable-hidden"
      });
    }
  }

  render() {
    return (
      <div className="form-group input-form-control-wrapper">
        <label htmlFor={this.props.name} className={this.state.inputLabel}>
          {this.props.text}
        </label>
        <input
          autoComplete="off"
          type={this.props.type}
          className="form-control input-form-input"
          name={this.props.name}
          required
          aria-describedby={this.props.name}
          onChange={e => {
            this.props.setFormData(e);
          }}
          value={this.state.value}
          onFocus={e => {
            this.setFocusStyle(e);
          }}
          onBlur={e => {
            this.setBlurStyle(e);
          }}
        />
      </div>
    );
  }
}
export default Input;
