import React, { Component } from "react";
import { Button, Modal } from "react-bootstrap";
import axios from "axios";
import _ from "lodash";
import config from "../../config";
import Spinner from "../../components/Spinner/Spinner";
import Input from "../../components/Inputs/Input";
import Checkbox from "../../components/Inputs/Checkbox";
import LQSelect from "../../components/Inputs/Select";
import { Scrollbars } from "react-custom-scrollbars";

import "./LiquidityManagement.css";
const frequency_options = [
  { value: "hourly", label: "Hourly" },
  { value: "overnight", label: "Overnight" }
];
const frequency_parameters = [
  { value: "2_hours", label: "2 hours" },
  { value: "4_hours", label: "4 hours" }
];
const rule_types_options = [
  { value: "excess_sweep", label: "Excess sweep" },
  { value: "maintain_threshold", label: "Maintain threshold" }
];

class LiqidityManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: props.show,
      loading: true,
      accounts: null,
      threshHolds: null,
      activeAccount: null,
      selectedOption: null,
      baseAccount: "Base account",
      sweepOptions: {},
      showfrequencyParameter: true,
      isChecked: true,
      sweepDefaultValue: "base_account",
      frequencyDefault: "Hourly",
      frequencyParamDefault: "2 hours",
      ruleTypeDefault: "Excess sweep"
    };
  }
  componentDidMount = () => {
    let clientId = sessionStorage.getItem("clientId");
    if (clientId) {
      this.loadAccounts(clientId);
    } else {
      this.setState({
        notification: "unable to load client id"
      });
    }
  };
  loadAccounts = clientId => {
    let that = this;
    axios({
      method: "get",
      url: config.ACCOUNT_URL + clientId,
      responseType: "json"
    })
      .then(resp => {
        that.state.accounts = resp.data;
        this.setState(
          {
            activeAccount: resp.data[0]
          },
          () => {
            this.loadThreshold(resp.data[0]);
            this.loadSweepAccount(resp.data);
          }
        );
      })
      .catch(e => {
        console.log("exception while loading accounts ", e);
        this.setState({
          notification: "Something went wrong",
          loading: false
        });
      });
  };
  loadThreshold = account => {
    this.resetState();
    this.setState({
      loading: true
    });
    let that = this;
    setTimeout(() => {
      axios({
        method: "get",
        url: config.THRESHOLD_URL + account.accountId,
        responseType: "json"
      })
        .then(resp => {
          that.state.threshHolds = resp.data;
          that.loadRules(account, resp.data);
        })
        .catch(e => {
          console.log("exception while loading accounts ", e);
          this.setState({
            notification: "Something went wrong",
            loading: true
          });
        });
    }, 100);
  };

  truncate = s => {
    if (s.length > 10) {
      return s.substring(0, 16) + "...";
    } else return s;
  };

  getAccounts() {
    let _accountId = this.state.activeAccount.accountId;
    return (
      <Scrollbars autoHide autoHideTimeout={1000} style={{ height: 375 }}>
        <ul className="m-accounts">
          {this.state.accounts !== null &&
            this.state.accounts.map((account, index) => {
              return (
                <li
                  key={index}
                  title={account.counterParty + " " + account.accountId}
                  className={
                    _accountId === account.accountId
                      ? "lq-list lq-active"
                      : "lq-list"
                  }
                  onClick={() => {
                    this.loadThreshold(account);
                  }}
                >
                  <span className="lq-list-title">{account.accountType}</span>
                  <span
                    className="lq-list-account"
                    style={{ overflow: "hidden", textOverflow: "ellipsis" }}
                  >
                    {" "}
                    {this.truncate(
                      account.counterParty + " " + account.accountId
                    )}
                  </span>
                  <span className="lq-list-flag">
                    <i className={"flag " + account.currency} />
                    {account.currency} account
                  </span>
                </li>
              );
            })}
        </ul>
      </Scrollbars>
    );
  }

  loadRules(account, accountObj) {
    this.setState({
      loading: false,
      activeAccount: account,
      threshHolds: accountObj,
      lowerLimit:
        (accountObj.liquidityLimitDto &&
          accountObj.liquidityLimitDto.lowerLimit) ||
        "",
      sweepDefaultValue:
        (accountObj.liquidityLimitDto &&
          accountObj.liquidityLimitDto.sweepAccount) ||
        this.state.sweepDefaultValue,
      upperLimit:
        (accountObj.liquidityLimitDto &&
          accountObj.liquidityLimitDto.upperLimit) ||
        "",
      frequencyDefault:
        (accountObj.liquidityRuleDto &&
          accountObj.liquidityRuleDto.frequency) ||
        this.state.frequencyDefault ||
        "Hourly",
      frequencyParamDefault:
        (accountObj.liquidityRuleDto &&
          accountObj.liquidityRuleDto.frequencyParam) ||
        this.state.frequencyParamDefault ||
        "2 hours",
      ruleTypeDefault:
        (accountObj.liquidityRuleDto && accountObj.liquidityRuleDto.ruleType) ||
        this.state.ruleTypeDefault ||
        "Excess sweep",
      isChecked:
        (accountObj.liquidityRuleDto && accountObj.liquidityRuleDto.enable) ||
        true
    });
  }

  handleClose() {
    this.props.handleClose();
  }

  setFormData(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  handleChange = (selectedOption, name) => {
    if (name === "frequency") {
      if (selectedOption.target.value === "overnight") {
        this.setState({
          showfrequencyParameter: false
        });
      } else {
        this.setState({
          showfrequencyParameter: true
        });
      }
    }
    this.setState({ [name]: selectedOption.target.value });
  };

  updateThreshold() {
    let that = this;
    let activeAccount = this.state.activeAccount;
    let postObject = {
      liquidityLimitDto: {
        clientId: activeAccount.clientDetails.clientId,
        accountId: activeAccount.accountId,
        accountCCY: activeAccount.currency,
        upperLimit: this.state.upperLimit || "",
        lowerLimit: this.state.lowerLimit || "",
        sweepAccount:
          this.state.sweepAccount || this.state.sweepDefaultValue || ""
      },
      liquidityRuleDto: {
        accountId: activeAccount.accountId,
        clientId: activeAccount.clientDetails.clientId,
        enable: this.state.isChecked,
        frequency: this.state.frequency || "hourly",
        frequencyParam: this.state.frequencyParameter || "2_hours",
        ruleType: this.state.ruleType || "excess_sweep"
      }
    };
    console.log("postObject ", postObject);
    //return;
    that.setState({
      loading: true
    });
    axios
      .post(config.THRESHOLD_URL, postObject)
      .then(function(response) {
        that.setState({
          loading: false
        });
      })
      .catch(function(error) {
        that.setState({
          loading: false
        });
      });
  }

  loadSweepAccount() {
    let _activeAccount = this.state.activeAccount;
    let accounts = this.state.accounts;
    let sweep_options = [
      {
        value: "",
        label: "Base account"
      }
    ];
    _.forEach(accounts, function(value, key) {
      if (_activeAccount.accountId !== value.accountId) {
        sweep_options.push({
          value: value.accountId,
          label: value.accountId
        });
      }
    });
    return sweep_options;
  }
  rendrSweepAccount() {
    let sweep_options = this.loadSweepAccount();
    let placeholder = this.state.baseAccount;
    if (
      this.state.threshHolds &&
      this.state.threshHolds.liquidityLimitDto &&
      this.state.threshHolds.liquidityLimitDto.sweepAccount
    ) {
      placeholder = this.state.threshHolds.liquidityLimitDto.sweepAccount;
    } else {
      placeholder = "Base account";
    }
    //console.log("placeholder ", placeholder, this.state);

    return (
      <LQSelect
        placeholder={placeholder}
        defaultValue={this.state.sweepDefaultValue}
        options={sweep_options}
        name="sweepAccount"
        label="Sweep account"
        handleChange={e => {
          this.handleChange(e, "sweepAccount");
        }}
      />
    );
  }

  changed(e) {
    this.setState({
      isChecked: !this.state.isChecked
    });
  }

  resetState() {
    this.setState({
      sweepAccount: "",
      sweepDefaultValue: "",
      upperLimit: "",
      lowerLimit: "",
      frequency: "",
      frequencyParameter: "",
      ruleType: "",
      ruleTypeDefault: "",
      frequencyParamDefault: "",
      frequencyDefault: ""
    });
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        onHide={() => {
          this.handleClose();
        }}
      >
        <Modal.Header closeButton={false}>
          <Modal.Title>Liquidity management</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {this.state.loading === true && (
            <Spinner class="spinner__content_on_modal" height={300} />
          )}
          {!this.state.loading && (
            <div className="lq-modal-body">
              <div className="row">
                <div className="col col-sm-4">
                  <div className="lq-modal-left">
                    {this.state.activeAccount &&
                      this.state.activeAccount !== null &&
                      this.getAccounts()}
                  </div>
                </div>
                <div className="col col-sm-8">
                  <div className="lq-modal-right">
                    <h5 className="lq-sub-heading">Threshold settings</h5>
                    <div className="row">
                      <div className="col col-sm">
                        <Input
                          type="text"
                          name="upperLimit"
                          text={"upper threshhold"}
                          setFormData={e => {
                            this.setFormData(e);
                          }}
                          value={this.state.upperLimit}
                        />
                      </div>
                      <div className="col col-sm">
                        <Input
                          type="text"
                          name="lowerLimit"
                          text={"lower threshhold"}
                          setFormData={e => {
                            this.setFormData(e);
                          }}
                          value={this.state.lowerLimit}
                        />
                      </div>
                      <div className="col col-sm">
                        {this.state.accounts !== null &&
                          this.rendrSweepAccount()}
                      </div>
                    </div>
                    <hr />
                    <h5 className="lq-sub-heading">
                      Liquidy rules{" "}
                      <Checkbox
                        label="Enable"
                        isChecked={this.state.isChecked}
                        customClass="lq-ckb"
                        changed={e => {
                          this.changed(e);
                        }}
                      />
                    </h5>
                    <div />
                    <div className="row">
                      <div className="col col-sm">
                        <LQSelect
                          placeholder={this.state.frequencyDefault}
                          defaultValue={this.state.frequencyDefault}
                          options={frequency_options}
                          isDisabled={!this.state.isChecked}
                          name="frequency"
                          label="Frequency"
                          value={this.state.frequency}
                          handleChange={e => {
                            this.handleChange(e, "frequency");
                          }}
                        />
                      </div>
                      {this.state.showfrequencyParameter && (
                        <div className="col col-sm">
                          <LQSelect
                            placeholder={this.state.frequencyParamDefault}
                            defaultValue={this.state.frequencyParamDefault}
                            options={frequency_parameters}
                            isDisabled={!this.state.isChecked}
                            name="frequencyParameter"
                            label="Frequency Parameter"
                            value={this.state.frequencyParameter}
                            handleChange={e => {
                              this.handleChange(e, "frequencyParameter");
                            }}
                          />
                        </div>
                      )}
                      <div className="col col-sm">
                        <LQSelect
                          placeholder={this.state.ruleTypeDefault}
                          defaultValue={this.state.ruleTypeDefault}
                          options={rule_types_options}
                          isDisabled={!this.state.isChecked}
                          name="ruleType"
                          label="Rule type"
                          value={this.state.ruleType}
                          handleChange={e => {
                            this.handleChange(e, "ruleType");
                          }}
                        />
                      </div>
                    </div>
                  </div>
                  <Modal.Footer>
                    <div className="lq-modal-footer">
                      <Button
                        variant="outline-secondary lg-close"
                        onClick={() => {
                          this.handleClose();
                        }}
                      >
                        Close
                      </Button>
                      <Button
                        variant="btn login-button-active"
                        onClick={() => {
                          this.updateThreshold();
                        }}
                      >
                        Save
                      </Button>
                    </div>
                  </Modal.Footer>
                </div>
              </div>
            </div>
          )}
        </Modal.Body>
      </Modal>
    );
  }
}
export default LiqidityManagement;
