import React, { Component } from "react";
import { Scrollbars } from "react-custom-scrollbars";
import axios from "axios";
import Navigation from "../../components/Navigation/Navigation";
import Accounts from "../../components/Accounts/Accounts";
import TransactionTable from "../../components/Table/Table";
import Footer from "../../components/Footer/Footer";
import Spinner from "../../components/Spinner/Spinner";
import config from "../../config";

import "./Dashboard.css";

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accounts: null,
      loading: true,
      balances: {},
      transactions: null,
      trasnactionLoading: true,
      moreTransactions: false,
      transactionsTitle: "Treasury transactions"
    };
  }
  componentDidMount = () => {
    this.start();
  };

  start() {
    let clientId = sessionStorage.getItem("clientId");
    if (clientId) {
      this.setState({
        clientId: clientId,
        accounts: null,
        loading: true,
        balances: {},
        transactions: null,
        trasnactionLoading: true
      });
      this.loadAccounts(clientId);
      this.loadTransactions(config.TRANSACTIONS_URL + clientId);
    } else {
      this.setState({
        notification: "unable to load client id"
      });
    }
  }

  loadAccounts = clientId => {
    let that = this;
    axios({
      method: "get",
      url: config.ACCOUNT_URL + clientId,
      responseType: "json"
    })
      .then(resp => {
        that.state.accounts = resp.data;
        that.loadBalances(resp.data);
      })
      .catch(e => {
        console.log("exception while loading accounts ", e);
        this.setState({
          loading: false,
          notification: "Something went wrong"
        });
      });
  };

  loadBalances = data => {
    let that = this;
    let accountObj = {};
    data.map((account, index) => {
      return axios({
        method: "get",
        url: config.BALANCE_URL + account.accountId,
        responseType: "json"
      })
        .then(resp => {
          accountObj[account.accountId] = resp.data;
          if (data.length - 1 === index) {
            that.state.balances = accountObj;
            setTimeout(() => {
              this.setState({
                loading: false
              });
            }, 500);
          }
        })
        .catch(e => {
          console.log("exception while loading balances ", e);
        });
    });
  };

  loadAccountComponents() {
    if (this.state.loading === true) return <Spinner height={400} />;
    if (
      this.state.loading === false &&
      this.state.accounts === null &&
      this.state.notification
    )
      return (
        <div className="login-form-text-main-err login-form-text-err">
          {this.state.notification}
        </div>
      );
    return (
      <Scrollbars autoHide autoHideTimeout={1000} style={{ height: 700 }}>
        {this.state.accounts !== null &&
          this.state.accounts.map((account, index) => {
            return (
              <Accounts
                loadAccountDetails={() => {
                  this.loadAccountDetails(account);
                }}
                account={account}
                balance={this.state.balances[account.accountId]}
                key={index}
              />
            );
          })}
      </Scrollbars>
    );
  }

  loadAccountDetails(account) {
    this.setState({
      transactions: null,
      trasnactionLoading: true,
      transactionsTitle:
        "Treasury transactions for " +
        account.counterParty +
        " " +
        account.accountId
    });
    this.loadTransactions(config.ACCOUNT_TRANSACTIONS_URL + account.accountId);
  }

  loadTransactions = url => {
    axios({
      method: "get",
      url: url,
      responseType: "json"
    })
      .then(resp => {
        let transactionsData = resp.data;
        this.setState({
          transactions: transactionsData,
          trasnactionLoading: false
        });
      })
      .catch(e => {
        console.log("exception while loading accounts ", e);
        this.setState({
          trasnactionLoading: false,
          transactionNotification: "Something went wrong"
        });
      });
  };
  renderTransactions() {
    if (
      this.state.trasnactionLoading === false &&
      this.state.transactions === null &&
      this.state.transactionNotification
    ) {
      return (
        <div className="login-form-text-main-err login-form-text-err">
          {this.state.transactionNotification}
        </div>
      );
    }
    if (this.state.transactions && this.state.transactions.length === 0) {
      return <div className="no-records">No records found</div>;
    }
    return (
      <Scrollbars autoHide autoHideTimeout={1000} style={{ height: 700 }}>
        <TransactionTable transactions={this.state.transactions} />
      </Scrollbars>
    );
  }
  render() {
    return (
      <div>
        <Navigation />
        <div className="container liquify-dashboard">
          <div className="row">
            <div className="col-sm-5 left-block">
              <h2
                className="section-title"
                onClick={() => {
                  this.start();
                }}
              >
                My accounts
              </h2>
              {this.loadAccountComponents()}
            </div>
            <div className="col-sm-7 right-block">
              <h2 className="section-title">
                {this.state.transactionsTitle}
                {this.state.transactions &&
                  this.state.transactions.length !== 0 && (
                    <i className="filter lg-filter float-right"> Filter</i>
                  )}
              </h2>
              {this.state.trasnactionLoading === true && (
                <Spinner height={400} />
              )}
              {this.renderTransactions()}
              {this.state.moreTransactions && (
                <button
                  type="button"
                  className="btn btn-secondary btn-sm btn-block transaction-btn"
                >
                  Load more transactions
                </button>
              )}
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
export default Dashboard;
