import React, { Component } from "react";
import "./Checkbox.css";

class Checkbox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputLabel: ""
    };
  }

  static getDerivedStateFromProps(props, state) {
    return state;
  }

  render() {
    return (
      <div className={this.props.customClass}>
        <span className="cb-lq-label">{this.props.label}</span>
        <label className="lq-switch">
          <input
            type="checkbox"
            checked={this.props.isChecked}
            onChange={this.props.changed}
          />
          <span className="lq-slider lq-round" />
        </label>
      </div>
    );
  }
}
export default Checkbox;
